.about {
    background-color: #f3f3f3;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.lane {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: 400;
    width: 100%;
    align-self: center;
    margin-bottom: 15px;
    font-size: 16px;
    background-color: #fff;
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
}

.lane:last-child{
    margin-bottom: 0;
}

.lanes  {
    padding: 25px 0 25px 0;
    display: flex;
    flex-direction: column;
    max-width: 650px;
    align-items: center;

}

.block-img {
    width: 25px;
    height: 25px;
    padding: 15px;
    padding-left: 0;
}

.text-block {
    display: flex;
    flex-direction: column;
}

.paragraph {
    margin: 5px;
}

@media screen and (max-width: 320px) {
    .lane {
        font-size: 14px;
        padding: 15px 10px 15px 10px;
        margin-bottom: 10px;
    }

    .lanes {
        padding: 20px 0 20px 0;
    }

    .block-img {
        padding: 5px 15px 5px 5px;
        width: 20px;
        height: 20px;
    }

    .paragraph {
        margin: 0;
    }
}