.color1 {color: #520f48;}
.color2 {color: #9c2057;}
.color3 {color: #6994d4;}
.color4 {color: #c9d9e8;}
.color5 {color: #f3f3f3;}

.page {
    width: 100%;
    display: flex;
    flex-direction: column;
}
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #9c2057;
    color: #fff;
    text-align: center;
    border-radius: 0 0 25px 25px;
}

.button {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: #9c2057;
    border: 2px solid #fff;
    color: #fff;
    transition: background-color, color, border, border-radius .2s ease-in-out;
    font-size: 14px;
    padding: 5px;
    font-weight: 400;
}

.button:hover {
    cursor: pointer;
    background-color: #59032b;
    border-radius: 0;
}

.button:first-child {
    margin-right: 20px;
}


.salary-block {
    padding: 25px;
}

.buttons-block {
    padding: 0 25px 25px;

}

.your-salary {
    margin: 0;
    font-size: 28px;

}

.current-period {
    margin: 0;
}

@media screen and (min-width: 1280px) {
    .page {
        max-width: 800px;
    }
}

@media screen and (max-width: 320px) {
    .button {
        width: 75px;
        height: 75px;
        font-size: 10px;
    }
}
