.logout {
    display: flex;
    flex-direction: row;
    align-items: center;
    font-weight: 400;
    justify-content: center;
    background-color: #9c2057;
    color: #fff;
    transition: background-color .3s linear;
    width: 100%;
    height: 50px;
    font-weight: 600;
    font-size: 14px;
}

.logout:hover {
    background-color: #59032b;
    cursor: pointer;
}