.color1 {color: #520f48;}
.color2 {color: #9c2057;}
.color3 {color: #6994d4;}
.color4 {color: #c9d9e8;}
.color5 {color: #f3f3f3;}



.login {
  display: flex;
  flex-direction: column;
  width: 90%;
  align-items: center;
  padding: 45px 0 45px 0;
}

.header-block {
  display: flex;
  justify-content: center;
  width: 100%;
  max-width: 450px;
  height: 100px;
  background-color: #9c2057;
  align-items: center;
  margin-bottom: 50px;
}

.header-logo {
  width: 50px;
  height: 50px;
}


.header-p {
  z-index: 9999;
  margin-bottom: 25px;
  font-size: 26px;
  color: #fff;
}

.main-block {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 9999;
  width: 100%;
  max-width: 450px;
  background-color: #f3f3f3;
  color: #000;
}

.input {
  width: 100%;
  height: 35px;
  margin: 0;
  padding-left: 30px;
  border: none;
  border-bottom: 1px solid #9c2057;
  transition: color .3s linear;
  background-color: #f3f3f3;
  font-size: 16px;
}

.button-signin {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #9c2057;
  color: #fff;
  transition: background-color .3s linear;
  width: 90%;
  height: 50px;
  font-size: 16px;
  font-weight: 600;
}

.button-signin:hover {
  background-color: #59032b;
  cursor: pointer;
}

.field-img {
  width: 20px;
  height: 20px;
  position: absolute;
  left: 35px;
}

.input-field {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: row;
  width: 90%;
  margin-bottom: 35px;
}

.input:focus-visible {
  outline: none;
}

@media screen and (min-width: 425px) {
  .field-img {
    left: 45px;
  }




}

@media screen and (max-width: 768px) {
  .button-signin {
    font-size: 14px;
  }

  .input {
    font-size: 14px;
  }
}